@import '../../sass/_mixins.scss';
@import '../../sass/Custom.bootstrap.scss';

$primary: #555;
$accent: rgba(22, 22, 22, 0.9);


/**
* Nav menu in Layout page, 
*/

    .navbar-header {
        .nav {
            display: inline-block;
            li {
                display: inline-block;
            }
        }
    }
    .span-item {
        // top: 10px;
        // left: 10px;
        margin: 0;
        padding: 0;
        font-size: 22px;
        font-weight: 700;
        color: transparent;
        background: linear-gradient(to right, #ddff99 10%, #ffdd99, #ffbb99 70%);
        -webkit-background-clip: text;
        background-clip: text;
        cursor: pointer;
        z-index: 1;
        &:hover {
            color: rgb(156, 168, 48);
        }
        &.fa-img {
            -webkit-background-clip: content-box;
            background-clip: content-box;
        }
    }
    .nav-body {
        z-index: 1199;
        height: 60vh;
        width: 100%;
        overflow-y: auto;
        @mixin transition {
            @for $i from 1 through 4 {
                &:nth-child(#{$i}) {
                    transition: all .2s #{$i * .1}s ease-in;
                }
            }
        }
        .l-item {
            @include transition;
        }
        
        .nav-item {
            position: relative;
            width: inherit;
            margin: 0;
            padding: 0 !important;
            min-height: 60px;
            // max-height: 80vh;
            left: 103%;
            background: $accent; 
            // box-shadow: 1px 0 3px 2px $accent;
            transition: all .3s cubic-bezier(.175, .885, .32, 1.275);
            @media only screen and (max-width: 767px) {
                padding-top: 20px;
            }
            @media only screen and (max-width: 400px) {
                padding-top: 45px;
            }
            &.active {
                left: 0;
            }
            .u-item {
                margin: 0 !important;
                padding: 5px 15px 20px 15px;
                position: relative;
                display: block;
                position: relative;
                // top: 50%;
                right: 10px;
                // transform: translateY(-50%);
                .l-item {
                    text-align: center;
                }
                & > a {
                    border: 1px solid yellow;
                    height: 35px;
                    width: 100%;
                    color: yellow;
                    .btn-icon-left {
                        margin: 0;
                        margin-top: -7px;
                        padding: 5px;
                        height: 35px;
                        width: 35px;
                        i {
                            height: 25px;
                            width: 25px;
                            line-height: 23px;
                        }
                    }
                    &.gradient-animation, &.gradient-animation-light {
                        border: none;
                    }
                }
                span {
                    &:first-of-type {
                        &.btn-icon-left {
                            border-right: none;
                        }
                    }
                }
                
                &.gradient {
                    .l-item {
                        @include transition;
                        // margin: 10px;
                        display: inline-block;
                        padding: 3px 0;
                        opacity: 0;
                        cursor: pointer;
                        &.active {
                            opacity: 1;
                            background: linear-gradient(to right, #ddff99 10%, #ffdd99, #ffbb99 70%);
                            &:hover {
                                @extend .my-focus;
                                background: linear-gradient(to right, #ddff99 10%, #ffdd99, #ffbb99 70%);
                                a {
                                    color: blue;
                                }
                            }
                        }
                    }
                }
                &.picture-box {
                    .l-item {
                        width: 190px;
                        height: 230px;
                        padding: 0;
                        margin: 10px;
                        .img-box {
                            width: 190px;
                            height: 190px;
                            display: block;
                            margin: 0;
                            padding: 0;
                            border: 1px solid transparent;
                            border-radius: 4px;
                            background: transparent;
                            img {
                                border-radius: 4px;
                                width: 190px;
                                height: 190px;
                                margin: 0;
                            }
                        }
                        a {
                            border: none;
                            border-top: none;
                            height: 40px;
                            padding: 0;
                            color: white;
                            overflow: hidden;
                            span {
                                height: 40px;
                                &:first-of-type {
                                    width: 20px;
                                    height: 100%;
                                    i {
                                        height: 20px;
                                        width: 20px;
                                        padding: 0;
                                        margin: 10px auto;
                                        font-size: 20px;
                                        line-height: 20px;
                                        overflow: visible;
                                        
                                    }
                                }
                                &:last-of-type {
                                    width: calc(100% - 20px);
                                    height: 100%;
                                    padding: 0;
                                    margin: 0 1px;
                                    white-space: normal;
                                    word-wrap: break-word;
                                    font-weight: 300;
                                    font-size: 11px !important;
                                    line-height: 1.4;
                                    @include mixin-text-overflow(ellipsis);
                                    &:hover {
                                        color: rgb(171, 142, 250);
                                    }
                                }
                                &.btn-icon-left {
                                    border-right: none;
                                }
                            }
                            i {
                                &.child-caret {
                                    border: 1px solid gold;
                                    position: absolute;
                                    top: 60%;
                                    left: 40%;
                                }
                            }
                        }
                    }
                    
                }
                .child-caret {
                    margin: 0 10px;
                    width: 20px;
                    height: 20px;
                    right: 0;
                    font-size: 20px;
                    line-height: 18px;
                    clear: right;
                    border: 1px solid blue;
                    border-radius: 4px;
                    &:hover {
                        color: yellow;
                        border-color: blueviolet;
                    }
                }
                .child-section {
                    position: relative;
                    width: 100%;
                    height: 100%;
                    overflow-y: auto;
                    display: block;
                    &.collapse {
                        &.in {
                            width: 0;
                            height: 0;
                            border: none;
                            overflow: hidden;
                        }
                    }
                }
                .opened {
                    .child-caret {
                        @include transform-rotate(90deg);
                        border: 1px solid yellow;
                        &:hover {
                            color: yellow;
                            border-color: blueviolet;
                        }
                    }
                }
                .span-item {
                    font-size: 14px;
                }
                .root-btn, .child-btn {
                    .fa-img {
                        overflow: visible;
                        height: 20px;
                        width: 20px;
                    }
                }
                .root-btn {
                    .fa-img {
                        height: 25px;
                        width: 25px;
                    }
                }
            }
            
        }
        &.full-screen {
            height: calc(100vh - 50px);
            .control.toolbar-base {
                right: 10px;
            }
        }
        .counter-on-nav {
            top: calc(50vh - 20px);
            left: calc(50vw - 20px);
            font-size: 20px;
            font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
            position: absolute;
            z-index: 1101;
            color: greenyellow;
        }
        .first-child, .last-child {
            // color: red;
            display: none;
        }
    }

/**
* end Nav menu in Layout page
*/

/** 
* toolbar for nav menu
*/
    
    .control.toolbar-base {
        position: absolute;
        right: 10px;
        color: #fff;
        width: 30px;
        height: 30px;
        font-size: 22px;
        text-align: center;
        border-radius: 50%;
        background-color: #2196F3;
    }
    .control {
        z-index: 100;
        cursor: pointer;
        i {
            cursor: pointer;
            width: 25px;
        }
    }
    .nav-toolbar {
        position: absolute;
        z-index: 9;
        background-color: transparent;
        text-align: center;
        font-size: 16px;
        border: none;
        display: none;
        &.active {
            display: flex;
        }
        .toolbar-body {
            display: flex;
            color: #fff;
            .control {
                padding: 5px;
                z-index: 10;
                background-color: #2196F3;
                i {
                    width: 35px;
                }
                input {
                    text-align: center;
                    width: 40px;
                    color: purple;
                }
            }
            
        }
        .toolbar-drag-selector {
            padding: 5px;
            cursor: move;
            z-index: 10;
            background-color: #2196F3;
            color: #fff;
        }
    }
